import React from 'react';

import '../styles/card.scss';

const IMG_URL = 'https://d3i4yxtzktqr9n.cloudfront.net/web-eats-v2';

const Card = ({ restaurant }) => (
  <div className="card">
    <div className="card-image__wrapper">
      <img
        src={restaurant.heroImageUrl}
        alt={restaurant.title}
        className="card-image"
      />
    </div>

    <h3 className="card-title">{restaurant.title}</h3>
    <span
      className="card-breadcrumbs"
      title={restaurant.categories.join(' • ')}
    >
      {restaurant.categories.join(' • ')}
    </span>

    <div className="card-additional">
      {
        restaurant.etaRange
          ? <div className="card-time">{restaurant.etaRange.text}</div>
          : <div className="card-time">50-60min</div>
      }
      <div className="card-rating">
        {
          restaurant.feedback
            ? (
              <>
                {restaurant.feedback.rating}
                <img
                  src={`${IMG_URL}/92367108b11b8ee48b6f29cb3fef2d4d.svg`}
                  alt="star"
                />
                ({restaurant.feedback.ratingCount})
              </>
            )
            : 'No rating'
        }
      </div>
    </div>
  </div>
);

export default Card;
