import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

import '../styles/header.scss';

const IMG_URL = 'https://d3i4yxtzktqr9n.cloudfront.net/web-eats-v2';

const Header = () => {
  const [initialFields, setInitialFields]
    = useState({
      location: localStorage.getItem('location') || 'London',
      search: localStorage.getItem('search') || '',
    });

  const [fields, setFields] = useState(initialFields);

  const [editing, setEditing] = useState({ location: false, search: false, delivery: false });

  const inputRef = useRef();

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    if (editing.location || editing.search) {
      const field = editing.search ? 'search' : 'location';

      inputRef.current[field].focus();
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  });

  const handleClick = (event) => {
    const { name } = event.target;

    setEditing({ ...editing, [name]: true });
  };

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFields({ ...fields, [name]: value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const { name } = event.target;
    const { value } = event.target[name];

    localStorage.setItem(name, value);

    setInitialFields({
      ...initialFields,
      [name]: localStorage.getItem(name),
    });
    setEditing({ ...editing, [name]: false });
  };

  const handleClickOutside = (event) => {
    if (inputRef.current && !inputRef.current.contains(event.target)) {
      setFields({ ...initialFields });
      setEditing({ location: false, search: false });
    }
  };

  return (
    <header className="header">
      <div className="container header__container">
        <div className="header__container-left">
          <Link to="/">
            <img
              src={`${IMG_URL}/f8f0721f871b3704cce92eb96bc6e504.svg`}
              alt="Uber Eats"
            />
          </Link>

          {
            editing.location
            && (
              <form
                action=""
                name="location"
                onSubmit={event => handleSubmit(event)}
                ref={inputRef}
                className="header__location header__location-form"
              >
                <input
                  type="text"
                  name="location"
                  value={fields.location}
                  onChange={event => handleChange(event)}
                />
              </form>
            )
          }

          {
            editing.location
            || (
              <button
                type="button"
                name="location"
                onClick={event => handleClick(event)}
                className="header__location"
              >
                {initialFields.location || 'Location'}
              </button>
            )
          }
          <div className="header__delivery-wrapper">
            <button
              name="delivery"
              type="button"
              className="header__delivery"
              onClick={event => handleClick(event)}
            >
              Deliver now
            </button>
            {
              editing.delivery && (
                <div className="header__delivery-select">
                  <button type="button" className="header__delivery-select-now">
                    Deliver now
                  </button>
                  <button
                    type="button"
                    className="header__delivery-select-time"
                  >
                    Deliver at time
                  </button>
                </div>
              )
            }
          </div>
        </div>
        <div className="header__container-right">
          {
            editing.search
            && (
              <form
                action=""
                name="search"
                onSubmit={event => handleSubmit(event)}
                ref={inputRef}
                className="header__search header__search-form"
              >
                <input
                  type="text"
                  name="search"
                  value={fields.search}
                  onChange={event => handleChange(event)}
                />
              </form>
            )
          }

          {
            editing.search
            || (
              <button
                type="button"
                name="search"
                onClick={event => handleClick(event)}
                className="header__search"
              >
                { fields.search || 'Search'}
              </button>
            )
          }

          <a href="#" className="header__sign-in">Sign in</a>
        </div>
      </div>
    </header>
  );
};

export default Header;
