import React, { useEffect, useState } from 'react';
import axios from 'axios';

import Cards from './Cards';
import '../styles/main.scss';

const BASE_URL = 'https://mate-academy.github.io/react_uber-eats/api/';
const API_URL = `${BASE_URL}location/ChIJdd4hrwug2EcRmSrV3Vo6llI.json`;

const Main = () => {
  const [restaurants, setRestaurants] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    axios.get(API_URL)
      .then((res) => {
        const result = res.data.data;
        const rests = result.feedItems.map(item => result.storesMap[item.uuid]);

        setRestaurants(rests);
        setLoading(false);
      });

    return () => setLoading(true);
  }, []);

  return (
    <main>
      <div className="container">
        <div className="grid-container">
          {
            loading
              ? <div>Loading</div>
              : <Cards restaurants={restaurants} />
          }
        </div>
      </div>
    </main>
  );
};

export default Main;
